.history {
  margin: 0;
  width: 100%;
  min-height: 100vh;
  margin: 0;
  padding: 32px 0 0 0;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  position: relative;
  background: #33284C;
}

.history__container {
  margin: 0 auto;
  max-width: 1440px;
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

@media all and (max-width: 1440px) {
  .history__container {
    padding: 0 20px;
    box-sizing: border-box;
  }
}

@media all and (max-width: 1000px) {
  .history {
    padding: 16px 0 0 0;
  }
  .history__container {
    padding: 0;
  }
}
